<template>
  <div id="app1">
    <audio id="audio" ref="audio" autoplay loop>
      <source src="assets/music/sixNine.mp3" type="audio/mpeg" />
    </audio>
    <div class="box">
      <div class="left" :class="isTrue ? 'l' : ''">{{ name }}</div>
      <div class="right" :class="isTrue ? 'r' : ''">圣诞快乐</div>
      <img
        src="https://image2.135editor.com/cache/remote/aHR0cHM6Ly9tbWJpei5xbG9nby5jbi9tbWJpel9naWYvb0paV1RwSnBpYWU5bWU4dVVHY2ZHVmlhS1ZZeWliVlc3V2hUeWR1Wm1kR0NBaE52NHhpYmlhR3hzRERiR0htcGdVTWtmY1BMaWFjNk9FdzcyelpBQlhRRGsxQlEvNjQw"
        class="but"
        :class="isTrue ? 'none' : ''"
        @click="butt"
      />
    </div>
  </div>
</template>
<script>
import * as THREE from "three";
import { GLTFLoader } from "three/addons/loaders/GLTFLoader.js";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
export default {
  data() {
    return {
      a: null,
      isTrue: 0,
      name: `圣诞快乐`,
    };
  },
  computed: {
    renderer: () => {
      return new THREE.WebGLRenderer();
    },
    camera: () => {
      return new THREE.PerspectiveCamera(
        75,
        window.innerWidth / window.innerHeight,
        0.1,
        1000
      );
    },
    scene: () => {
      return new THREE.Scene();
    },
  },

  methods: {
    autoMusic: () => {
      let audio;
      let s = setInterval(() => {
        if (audio) {
          console.log(111, audio);
          audio.play();
          clearInterval(s);
        } else {
          audio = document.getElementById("audio");
        }
      }, 50);
    },
    butt: function () {
      this.isTrue = true;
      let audio = document.getElementById("audio");
      audio.play();
    },
  },
  mounted() {
    if (this.$route.query.name) {
      this.name = this.$route.query.name;
    }
    console.log(this.$route.query.name);
    const that = this;
    // setTimeout(() => {
    //   console.log(that.$refs);
    //   that.$refs.audio.play();
    // }, 500);

    let mixer = null;
    this.renderer.setSize(window.innerWidth, window.innerHeight);
    document.body.querySelector("#app1").appendChild(this.renderer.domElement);
    this.camera.position.set(0, 0, 3);

    /**
     * Light
     */
    const directionLight = new THREE.DirectionalLight();
    directionLight.castShadow = true;
    directionLight.position.set(5, 5, 6);
    directionLight.shadow.camera.near = 1;
    directionLight.shadow.camera.far = 20;
    directionLight.shadow.camera.top = 10;
    directionLight.shadow.camera.right = 10;
    directionLight.shadow.camera.bottom = -10;
    directionLight.shadow.camera.left = -10;

    const directionLightHelper = new THREE.DirectionalLightHelper(
      directionLight,
      2
    );
    directionLightHelper.visible = false;
    this.scene.add(directionLightHelper);

    const directionalLightCameraHelper = new THREE.CameraHelper(
      directionLight.shadow.camera
    );
    directionalLightCameraHelper.visible = false;
    this.scene.add(directionalLightCameraHelper);

    const ambientLight = new THREE.AmbientLight(
      new THREE.Color("#ffffff"),
      0.3
    );
    this.scene.add(ambientLight, directionLight);

    const loader = new GLTFLoader();

    loader.load(
      "assets/christmas_tree.glb",
      function (gltf) {
        that.scene.add(gltf.scene);
        that.a = gltf.scene;
        mixer = new THREE.AnimationMixer(gltf.scene);
        const action = mixer.clipAction(gltf.animations[0]);
        action.play();
      },
      undefined,
      function (error) {
        console.error(error);
      }
    );

    const clock = new THREE.Clock();
    let previousTime = 0;
    function animate() {
      const elapsedTime = clock.getElapsedTime();
      const deltaTime = elapsedTime - previousTime;
      previousTime = elapsedTime;

      // update mixer
      if (mixer) {
        mixer.update(deltaTime);
      }

      requestAnimationFrame(animate);
      if (that.a && that.isTrue) that.a.rotation.y += 0.01;
      that.renderer.render(that.scene, that.camera);
    }
    animate();
  },
};
</script>
<style lang="less">
.box {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  // background: #fff;
  > div {
    position: absolute;
    transition: all 0.3s;
  }
  .left,
  .right {
    width: 50%;
    height: 100%;
    z-index: 2;
    font-family: "汉仪";
    font-size: 8rem;
    display: flex;
    justify-content: center;
    align-items: center;
    writing-mode: vertical-lr;
  }
  .left {
    background: #be0214;
    color: #fecb49;
  }
  .right {
    left: 50%;
    background: #fecb49;
    color: #be0214;
  }
  .but {
    width: 300px;
    height: 150px;
    position: absolute;
    border-radius: 10px;
    z-index: 3;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    cursor: pointer;
    border: 3px solid #fff;
  }
  .l {
    transform: translateX(-100%);
  }
  .r {
    transform: translateX(100%);
  }
  .none {
    opacity: 0;
  }
}
</style>
